<template>
  <div class="home">
    <Toolbar />

    <main>
      <!-- 左侧组件列表 -->
      <section class="left">
        <!-- <ComponentList /> -->
        <Menu />
      </section>
      <!-- 中间画布 -->
      <section id="center" class="center">
        <div class="cut-canvas" id="cutCanvas">
          <BoxmodelView />
          <div
            class="content"
            id="content"
            @drop="handleDrop"
            @dragover="handleDragOver"
            @mousedown="handleMouseDown"
            @mouseup="deselectCurComponent"
          >
            <Editor />
          </div>
        </div>
      </section>
      <!-- 右侧属性列表 -->
      <section class="right">
        <el-tabs v-model="activeName" class="tab-wrap">
          <el-tab-pane label="画布" name="canvas">
            <el-form :label-position="'left'" label-width="80px" class="canvas-config">
              <p>选择的棱形内径应减x：</p>
              <el-radio-group v-model="prismRadio" :style="{ marginBottom: '30px' }">
                <el-radio :style="{ fontSize: '18px' }" :label="2">3层B（x=2）</el-radio>
                <el-radio :style="{ fontSize: '18px' }" :label="1">3层E（x=1）</el-radio>
                <el-radio :style="{ fontSize: '18px' }" :label="3">5层BE（x=3）</el-radio>
                <el-radio :style="{ fontSize: '18px' }" :label="4">5层CB（x=4）</el-radio>
              </el-radio-group>
              <p>长(输入外径尺寸，内径为外径-x)</p>
              <el-form-item label="外径尺寸">
                <el-input v-model="boxModelsData.length" type="number" @input="boxModalChange = true"></el-input>
                <span>mm</span>
              </el-form-item>
              <p>宽(输入外径尺寸，内径为外径-x)</p>
              <el-form-item label="外径尺寸">
                <el-input v-model="boxModelsData.width" type="number" @input="boxModalChange = true"></el-input>
                <span>mm</span>
              </el-form-item>
              <p>高(输入外径尺寸，内径为外径-x)</p>
              <el-form-item label="外径尺寸">
                <el-input v-model="boxModelsData.height" type="number" @input="boxModalChange = true"></el-input>
                <span>mm</span>
              </el-form-item>
              <!-- <el-form-item label="厚度">
                <el-input v-model="boxModelsData.thickness" type="number" @input="boxModalChange = true"></el-input>
                <span>mm</span>
              </el-form-item> -->
              <el-form-item label="缩放">
                <el-input v-model="scale" @input="scaleChanged = true"></el-input>
                <span>%</span>
              </el-form-item>
            </el-form>
            <p :style="{ width: '100%', textAlign: 'center' }">
              <el-button :disabled="!(boxModalChange || scaleChanged)" @click="handleConfirmChange" type="primary"
                >确认修改</el-button
              >
            </p>
          </el-tab-pane>
          <el-tab-pane label="属性" name="attr">
            <AttrList v-if="curComponent" />
            <p v-else class="placeholder">请选择组件</p>
          </el-tab-pane>
          <!-- <el-tab-pane label="动画" name="animation">
            <AnimationList v-if="curComponent" />
            <p v-else class="placeholder">请选择组件</p>
          </el-tab-pane>
          <el-tab-pane label="事件" name="events">
            <EventList v-if="curComponent" />
            <p v-else class="placeholder">请选择组件</p>
          </el-tab-pane> -->
        </el-tabs>
      </section>
    </main>
  </div>
</template>

<script>
import Editor from "@/components/Editor/index";
import Menu from "@/components/Menu";
import AttrList from "@/components/AttrList"; // 右侧属性列表
import AnimationList from "@/components/AnimationList"; // 右侧动画列表
import EventList from "@/components/EventList"; // 右侧事件列表
// import componentList from "@/custom-component/component-list"; // 左侧列表数据
import { List } from "../custom-component/component-list";
import Toolbar from "@/components/Toolbar";
import { deepCopy } from "@/utils/utils";
import { mapState } from "vuex";
import generateID from "@/utils/generateID";
import { listenGlobalKeyDown } from "@/utils/shortcutKey";
import BoxmodelView from "../components/BoxModelView";

export default {
  components: { Editor, Menu, AttrList, AnimationList, EventList, Toolbar, BoxmodelView },
  data() {
    return {
      activeName: "canvas",
      reSelectAnimateIndex: undefined,
      needToChange: ["top", "left", "width", "height", "fontSize", "borderWidth"],
      timer: null,
      scale: 20,
      scaleChanged: false,
      boxData: {
        length: 430,
        width: 210,
        height: 270,
        thickness: 3
      },
      boxModalChange: false
    };
  },
  computed: mapState([
    "componentData",
    "curComponent",
    "isClickComponent",
    "canvasStyleData",
    "editor",
    "boxModelsData",
    "currentBoxModel",
    "prismRadio"
  ]),
  created() {
    this.restore();
    // 全局监听按键事件
    listenGlobalKeyDown();
    // console.log("this.canvasStyleData.scale", this.canvasStyleData.scale);
    this.scale = this.canvasStyleData.scale;
  },
  mounted() {
    this.handleInitCanvasWrapStyle();
    // setTimeout(() => {
    //   console.log("componentData", JSON.stringify(this.componentData));
    //   console.log("boxModelsData", JSON.stringify(this.boxModelsData));
    //   console.log("canvasStyleData", JSON.stringify(this.canvasStyleData));
    // }, 2000);
  },
  methods: {
    handleConfirmChange() {
      if (this.boxModalChange) {
        this.$store.commit("setBoxModelsData", {
          length: +this.boxModelsData.length,
          width: +this.boxModelsData.width,
          height: +this.boxModelsData.height,
          thickness: +this.boxModelsData.thickness
        });
        this.boxModalChange = false;
      }
      if (this.scaleChanged) {
        this.handleScaleChange();
        this.scaleChanged = false;
      }
    },
    format(value) {
      const scale = this.scale;
      return (value * parseInt(scale)) / 100;
    },

    getOriginStyle(value) {
      const scale = this.canvasStyleData.scale;
      const result = value / (parseInt(scale) / 100);
      return result;
    },

    handleScaleChange() {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        // 画布比例设一个最小值，不能为 0
        // eslint-disable-next-line no-bitwise
        this.scale = ~~this.scale || 1;
        const componentData = deepCopy(this.componentData);
        componentData.forEach(component => {
          Object.keys(component.style).forEach(key => {
            if (this.needToChange.includes(key)) {
              // 根据原来的比例获取样式原来的尺寸
              // 再用原来的尺寸 * 现在的比例得出新的尺寸
              // 不能用 Math.round 防止 1 px 的边框变 0
              component.style[key] = Math.ceil(this.format(this.getOriginStyle(component.style[key])));
            }
          });
        });

        this.$store.commit("setComponentData", componentData);
        this.$store.commit("setCanvasStyle", {
          ...this.canvasStyleData,
          scale: this.scale
        });
      }, 100);
    },

    handleSetBoxModelsData(e, type) {
      console.log("boxLengthChange", e, type);
      this.$store.commit("setBoxModelsData", {
        [type]: +e.target.value
      });
    },

    handleInitCanvasWrapStyle() {
      if (!localStorage.getItem("canvasStyle")) {
        const width = document.getElementById("center").clientWidth - 20 - 20 - 2;
        const height = document.getElementById("center").clientHeight - 20 - 20 - 2;
        this.$store.commit("setCanvasStyle", {
          width: width / (this.scale / 100),
          height: height / (this.scale / 100)
        });
      }
    },
    restore() {
      // 用保存的数据恢复画布
      if (localStorage.getItem("canvasData")) {
        this.$store.commit("setComponentData", this.resetID(JSON.parse(localStorage.getItem("canvasData"))));
      }

      if (localStorage.getItem("canvasStyle")) {
        this.$store.commit("setCanvasStyle", JSON.parse(localStorage.getItem("canvasStyle")));
      }
      if (localStorage.getItem("boxModelsData")) {
        this.$store.commit("setBoxModelsData", JSON.parse(localStorage.getItem("boxModelsData")));
      }
      if (localStorage.getItem("backgroundColor")) {
        this.$store.commit("changeBackgroundColor", JSON.parse(localStorage.getItem("backgroundColor")));
      }
    },

    resetID(data) {
      data.forEach(item => {
        item.id = generateID();
      });

      return data;
    },

    handleDrop(e) {
      e.preventDefault();
      e.stopPropagation();
      const index = e.dataTransfer.getData("index");
      const rectInfo = this.editor.getBoundingClientRect();
      if (index) {
        const component = deepCopy(List[index]);
        component.style.top = e.clientY - rectInfo.y;
        component.style.left = e.clientX - rectInfo.x;
        component.id = generateID();
        this.$store.commit("addComponent", { component });
        this.$store.commit("recordSnapshot");
      }
    },

    handleDragOver(e) {
      e.preventDefault();
      e.dataTransfer.dropEffect = "copy";
    },

    handleMouseDown() {
      this.$store.commit("setClickComponentStatus", false);
    },

    deselectCurComponent(e) {
      if (!this.isClickComponent) {
        this.$store.commit("setCurComponent", { component: null, index: null });
      }

      // 0 左击 1 滚轮 2 右击
      if (e.button != 2) {
        this.$store.commit("hideContextMenu");
      }
    }
  }
};
</script>

