export function genZrenderPathPoints(points) {
  let ACount = 0;
  return points
    .map((item, index) => {
      if (index > 0) {
        const pre = points[index - 1];
        if (item.type === "L") {
          return new zrender.Line({
            shape: {
              x1: Number(pre.shape.x || pre.shape.x2 || 0),
              y1: Number(pre.shape.y || pre.shape.y2 || 0),
              x2: Number(item.shape.x),
              y2: Number(item.shape.y)
            }
          });
        }
        if (item.type === "A") {
          ACount++;
          return new zrender.BezierCurve({
            shape: item.shape
          });
        }
        if (item.type === "Q") {
          return new zrender.BezierCurve({
            shape: item.shape
          });
        }
        if (item.type === "Z") {
          return new zrender.Line({
            shape: {
              x1: Number(pre.shape.x || pre.shape.x2 || 0),
              y1: Number(pre.shape.y || pre.shape.y2 || 0),
              x2: Number(points[0].shape.x || points[0].shape.x2),
              y2: Number(points[0].shape.y || points[0].shape.y2)
            }
          });
        }
      } else {
        return null;
      }
    })
    .filter(item => !!item);
}

export function genZrenderPathStr(points) {
  let str = "";
  let ACount = 0;
  points.map((item, index) => {
    const pre = points[index - 1];
    // if (index !== 0) {
    //   str += "  ";
    // }
    if (item.type === "M") {
      str += `M${item.shape.x},${item.shape.y}`;
    }
    if (item.type === "L") {
      str += `L${item.shape.x},${item.shape.y}`;
    }
    if (item.type === "Q") {
      ACount++;
      const odd = ACount % 2 === 1;
      str += `Q${odd ? item.shape.x1 : item.shape.x2},${odd ? item.shape.y2 : item.shape.y1},${item.shape.x2},${
        item.shape.y2
      }`;
    }
    if (item.type === "A") {
      ACount++;
      const odd = ACount % 2 === 1;
      // str += `Q${odd ? item.shape.x1 : item.shape.x2},${odd ? item.shape.y2 : item.shape.y1},${item.shape.x2},${
      //   item.shape.y2
      // }`;
      str += `Q${item.shape.cpx1 ? item.shape.cpx1 : odd ? item.shape.x1 : item.shape.x2},${
        item.shape.cpy1 ? item.shape.cpy1 : odd ? item.shape.y2 : item.shape.y1
      },${item.shape.x2},${item.shape.y2}`;
    }
    if (item.type === "Z") {
      str += `Z `;
    }
  });
  return str;
}
