<template>
  <div class="menu">
    <el-tabs tab-position="left" style="height: 100%;">
      <el-tab-pane v-for="item in tabList" :label="item.text" :key="item.id">
        <DraggableText v-if="item.type === 'text'" />
        <BackGroundColor v-if="item.type === 'backgroundcolor'" />
        <el-upload
          v-if="item.type === 'picture'"
          class="upload-wrap"
          action="https://zx.carryba.com/addons/zjhj_mall/core/web/index.php?r=upload/save-img"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-success="handleUploadSuccess"
          :file-list="fileList"
          list-type="picture"
        >
          <p class="header">
            <el-button size="small" type="primary">上传图片</el-button>
            <el-button size="small" type="primary" @click="handleUsePic">使用</el-button>
          </p>
          <div slot="tip" class="el-upload__tip">
            只能上传jpg/png文件，且不超过500kb
          </div>
        </el-upload>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import DraggableText from "./DraggAbleText.vue";
import BackGroundColor from "./BackGroundColor.vue";
import generateID from "../utils/generateID";

export default {
  components: { DraggableText, BackGroundColor },
  data() {
    return {
      tabList: [
        { id: 2, text: "文字", type: "text" },
        {
          id: 1,
          text: "背景",
          type: "backgroundcolor"
        },
        {
          id: 3,
          text: "上传",
          type: "picture"
        }
      ],
      fileList: [],
      selectedPic: null,
      picComponentData: {
        animations: [],
        events: {},
        groupStyle: {},
        isLock: false,
        component: "Picture",
        label: "图片",
        icon: "tupian",
        style: {
          rotate: 0,
          opacity: 1,
          width: 300,
          height: 200,
          borderRadius: "",
          top: 250,
          left: 425
        }
      }
    };
  },
  created() {
    this.fileList = JSON.parse(localStorage.getItem("userPics")) || [];
  },
  methods: {
    handleRemove(file, fileList) {
      // console.log("handleRemove", file, fileList);
    },
    handlePreview(file) {
      // console.log("handlePreview", file);
      this.selectedPic = file;
    },
    handleUploadSuccess(response, file, fileList) {
      const formatFileList = fileList.map(item => ({
        name: item.name,
        url: item.response || item.url
      }));
      this.fileList = formatFileList;
      localStorage.setItem("userPics", JSON.stringify(formatFileList));
    },
    handleUsePic(e) {
      try {
        e.preventDefault();
        e.stopPropagation();
        const component = {
          ...this.picComponentData,
          propValue: this.selectedPic.url
        };
        const parentDom = document.getElementById("center");
        const parWidth = parentDom.clientWidth;
        const parHeight = parentDom.clientHeight;
        component.style.top = 300;
        component.style.left = 50;
        component.id = generateID();
        this.$store.commit("addComponent", { component });
        this.$store.commit("recordSnapshot");
        this.drawer = false;
        this.showBackGroundSelector = false;
        this.showUploadDrawer = false;
      } catch (error) {
        console.log("error", error);
      }
    }
  }
};
</script>
