<template>
  <div class="backgroundcolor-wrap">
    <el-color-picker v-model="color" show-alpha :predefine="predefineColors" @change="handleSelectColor">
    </el-color-picker>
    <div class="pre-color-list">
      <span
        v-for="colorItem in predefineColors"
        @click="handleSelectPreColor(colorItem)"
        :key="colorItem"
        class="pre-color-item"
        :style="{ backgroundColor: colorItem }"
      ></span>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      color: "#fff",
      predefineColors: [
        "#ff4500",
        "#ff8c00",
        "#ffd700",
        "#90ee90",
        "#00ced1",
        "#1e90ff",
        "#c71585",
        "rgba(255, 69, 0, 0.68)",
        "rgb(255, 120, 0)",
        "#c7158577"
      ]
    };
  },
  mounted() {
    this.color = this.backgroundColor || "#fff";
  },
  computed: mapState(["backgroundColor"]),
  methods: {
    handleSelectColor(color) {
      // console.log(color);
      if (color === null) {
        this.color = null;
        return;
      }
      let index = this.predefineColors.findIndex(item => item === color);
      if (index >= 0) {
        this.predefineColors.replace(index, 1);
      }
      this.predefineColors.unshift(color);
      this.predefineColors = this.predefineColors.slice(0, 18);
    },
    handleSelectPreColor(color) {
      // console.log(color);
      this.color = color;
    }
  },
  watch: {
    color(newColor, oldColor) {
      this.$store.commit("changeBackgroundColor", newColor);
    }
  }
};
</script>
<style lang="scss" scoped>
.backgroundcolor-wrap {
  margin: 20px;
}
.pre-color-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
  .pre-color-item {
    width: 22px;
    height: 22px;
    border-radius: 4px;
    margin-right: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
  }
}
</style>
