<template>
  <div class="tool-bar-wrap">
    <div class="toolbar">
      <el-button @click="undo">撤消</el-button>
      <el-button @click="redo">重做</el-button>
      <!-- <i class="el-icon-back btn" @click="undo" /> -->
      <!-- <i class="el-icon-right btn" @click="redo" /> -->
      <!-- <label for="input" class="insert">插入图片</label> -->
      <!-- <input id="input" type="file" hidden @change="handleFileChange" /> -->

      <el-button @click="save" style="margin-left: 10px">保存</el-button>
      <el-button @click="clearCanvas">清空画布</el-button>
      <!-- <el-button :disabled="!areaData.components.length" @click="compose">组合</el-button>
      <el-button
        :disabled="!curComponent || curComponent.isLock || curComponent.component != 'Group'"
        @click="decompose"
      >
        拆分
      </el-button> -->

      <!-- <el-button :disabled="!curComponent || curComponent.isLock" @click="lock">锁定</el-button>
      <el-button :disabled="!curComponent || !curComponent.isLock" @click="unlock">解锁</el-button> -->
      <!-- <div class="canvas-config">
        <span>画布大小</span>
        <input v-model="canvasStyleData.width" />
        <span>*</span>
        <input v-model="canvasStyleData.height" />
      </div>
      <div class="canvas-config">
        <span>画布比例</span>
        <input v-model="scale" @input="handleScaleChange" /> %
      </div> -->
    </div>
    <div>
      <!-- <el-button type="primary" @click="goBookPage">去下单</el-button> -->

      <!-- <el-button style="margin-left: 10px" type="primary" @click="preview">预览</el-button> -->
      <el-button type="primary" @click="exportPic">导出为图片</el-button>
    </div>
    <!-- 预览 -->
    <Preview v-model="isShowPreview" @change="handlePreviewChange" />
  </div>
</template>

<script>
import generateID from "@/utils/generateID";
import toast from "@/utils/toast";
import { mapState } from "vuex";
import Preview from "@/components/Editor/Preview";
import { commonStyle, commonAttr } from "@/custom-component/component-list";
import eventBus from "@/utils/eventBus";
import { deepCopy } from "@/utils/utils";
import html2canvas from "html2canvas";
import Canvg from "canvg";

export default {
  components: { Preview },
  data() {
    return {
      isShowPreview: false,
      picName: "img-snapshoot",
      needToChange: ["top", "left", "width", "height", "fontSize", "borderWidth"],
      scale: 22,
      timer: null
    };
  },
  computed: mapState([
    "componentData",
    "canvasStyleData",
    "areaData",
    "curComponent",
    "boxModelsData",
    "backgroundColor",
    "prismRadio"
  ]),
  created() {
    eventBus.$on("preview", this.preview);
    eventBus.$on("save", this.save);
    eventBus.$on("clearCanvas", this.clearCanvas);

    this.scale = this.canvasStyleData.scale;
  },
  mounted() {
    this.editorRef = document.getElementById("center");
    this.svgViewRef = document.getElementById("box-modle-wrap");
    this.editorViewRef = document.getElementById("editor");
  },
  methods: {
    format(value) {
      const scale = this.scale;
      return (value * parseInt(scale)) / 100;
    },

    getOriginStyle(value) {
      const scale = this.canvasStyleData.scale;
      const result = value / (parseInt(scale) / 100);
      return result;
    },

    handleScaleChange() {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        // 画布比例设一个最小值，不能为 0
        // eslint-disable-next-line no-bitwise
        this.scale = ~~this.scale || 1;
        const componentData = deepCopy(this.componentData);
        componentData.forEach(component => {
          Object.keys(component.style).forEach(key => {
            if (this.needToChange.includes(key)) {
              // 根据原来的比例获取样式原来的尺寸
              // 再用原来的尺寸 * 现在的比例得出新的尺寸
              // 不能用 Math.round 防止 1 px 的边框变 0
              component.style[key] = Math.ceil(this.format(this.getOriginStyle(component.style[key])));
            }
          });
        });

        this.$store.commit("setComponentData", componentData);
        this.$store.commit("setCanvasStyle", {
          ...this.canvasStyleData,
          scale: this.scale
        });
      }, 1000);
    },

    lock() {
      this.$store.commit("lock");
    },

    unlock() {
      this.$store.commit("unlock");
    },

    compose() {
      this.$store.commit("compose");
      this.$store.commit("recordSnapshot");
    },

    decompose() {
      this.$store.commit("decompose");
      this.$store.commit("recordSnapshot");
    },

    undo() {
      this.$store.commit("undo");
    },

    redo() {
      this.$store.commit("redo");
    },

    handleFileChange(e) {
      const file = e.target.files[0];
      if (!file.type.includes("image")) {
        toast("只能插入图片");
        return;
      }

      const reader = new FileReader();
      reader.onload = res => {
        const fileResult = res.target.result;
        const img = new Image();
        img.onload = () => {
          this.$store.commit("addComponent", {
            component: {
              ...commonAttr,
              id: generateID(),
              component: "Picture",
              label: "图片",
              icon: "",
              propValue: fileResult,
              style: {
                ...commonStyle,
                top: 0,
                left: 0,
                width: img.width,
                height: img.height
              }
            }
          });

          this.$store.commit("recordSnapshot");

          // 修复重复上传同一文件，@change 不触发的问题
          document.querySelector("#input").setAttribute("type", "text");
          document.querySelector("#input").setAttribute("type", "file");
        };

        img.src = fileResult;
      };

      reader.readAsDataURL(file);
    },

    preview() {
      this.isShowPreview = true;
      this.$store.commit("setEditMode", "preview");
    },

    save() {
      localStorage.setItem("canvasData", JSON.stringify(this.componentData));
      localStorage.setItem("canvasStyle", JSON.stringify(this.canvasStyleData));
      localStorage.setItem("boxModelsData", JSON.stringify(this.boxModelsData));
      localStorage.setItem("backgroundColor", JSON.stringify(this.backgroundColor));
      this.$message.success("保存成功");
    },

    clearCanvas() {
      this.$store.commit("setCurComponent", { component: null, index: null });
      this.$store.commit("setComponentData", []);
      this.$store.commit("recordSnapshot");
    },

    handlePreviewChange() {
      this.$store.commit("setEditMode", "edit");
    },

    // getStyle(obj, key) {
    //   return obj.currentStyle ? obj.currentStyle[key] : document.defaultView.getComputedStyle(obj, false)[key];
    // },

    async convertSvg2Canvas() {
      const svgElms = this.editorRef.getElementsByTagName("svg");
      const wrapEle = document.getElementById("content");
      html2canvas(wrapEle).then(function(canvas) {
        document.body.appendChild(canvas);
      });
    },

    async getPreviewImg() {
      await Promise.all([this.convertSvg2Canvas()]);
      const opts = {
        useCORS: true
      };
      const canvasObj = await html2canvas(this.editorRef, opts);
      var context = canvasObj.getContext("2d");
      //防止图片模糊的设置
      context.mozImageSmoothingEnabled = false;
      context.webkitImageSmoothingEnabled = false;
      context.msImageSmoothingEnabled = false;
      context.imageSmoothingEnabled = false;
      // png 格式图片
      let imgType = "image/png";
      this.canvasSrc = canvasObj.toDataURL(imgType);
      this.previewShowFlag = true;
    },

    exportPic() {
      this.$store.commit("setCurComponent", { component: null, index: null });
      this.$nextTick(() => {
        html2canvas(this.editorRef).then(function(canvas) {
          const triggerDownload = document.createElement("a");
          triggerDownload.setAttribute("href", canvas.toDataURL());
          triggerDownload.setAttribute("download", `下载图片${new Date().getTime()}.jpg`);
          triggerDownload.click();
        });

        html2canvas(this.svgViewRef).then(function(canvas) {
          const triggerDownload = document.createElement("a");
          triggerDownload.setAttribute("href", canvas.toDataURL());
          triggerDownload.setAttribute("download", `下载图片底图${new Date().getTime()}.jpg`);
          triggerDownload.click();
        });

        html2canvas(this.editorViewRef).then(function(canvas) {
          const triggerDownload = document.createElement("a");
          triggerDownload.setAttribute("href", canvas.toDataURL());
          triggerDownload.setAttribute("download", `下载图片其他${new Date().getTime()}.jpg`);
          triggerDownload.click();
        });
      });
    },

    goBookPage() {
      window.location = `pages/shopping/submit_order_general/submit_order_general?type=6&B=${this.boxModelsData.width}&H=${this.boxModelsData.height}$L=${this.boxModelsData.length}&prism=${this.prismRadio}`;
    }
  }
};
</script>

