<template>
  <div class="box-modle-2-wrap" id="box-modle-wrap"></div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import { genZrenderPathPoints, genZrenderPathStr } from "../utils/computedPoints";
export default {
  props: {},
  data() {
    return {
      strokeWidthLine: 0.25,
      // strokeWidthBorder: 0.4,
      // dasharray: [0.75, 0.5],
      fountSize: 4,

      length: 430,
      width: 210,
      height: 270,
      thickness: 3,
      strokeDasharray: [0.75, 0.5],

      zrInstance: null,
      zrEles: []
    };
  },
  created() {
    // console.log("zrender", zrender);
    if (!localStorage.getItem("boxModelsData")) {
      this.$store.commit("setBoxModelsData", {
        length: 430,
        width: 210,
        height: 270,
        thickness: 3
      });
    }
  },
  // updated() {
  //   console.log("update", this.boxModelsData);
  // },
  mounted() {
    this.zrInstance = zrender.init(document.getElementById("box-modle-wrap"), {
      renderer: "svg", //渲染方式 支持'canvas','svg','vml'
      with: "100%",
      height: "100%"
    });
    this.genSvg();
  },
  watch: {
    boxModelsData: function(newData, old) {
      // console.log("this.boxModelsData", old, newData);
      this.reDraw();
      // console.log("this.zrEles", this.zrEles);
    },
    // canvasStyleData(newData, old) {
    //   const mainWrap = document.getElementById("box-modle-2-wrap");
    //   const svg = mainWrap.getElementsByTagName("svg")[0];
    //   svg.style.transform = `scale(${newData.scale / 100})`;
    // },
    backgroundColor(newData, oldData) {
      this.zrEles.forEach(el => {
        el.attr({
          style: {
            fill: newData
          }
        });
      });
    }
  },
  computed: {
    ...mapState(["canvasStyleData", "boxModelsData", "backgroundColor"]),
    ...mapGetters(["calcBoxModelsData"]),
    scaleLine() {
      return this.strokeWidthLine / (this.canvasStyleData.scale / 100);
    },
    scaleBorder() {
      return this.strokeWidthBorder / (this.canvasStyleData.scale / 100);
    },
    scaleDasharray() {
      return `${this.dasharray[0] / (this.canvasStyleData.scale / 100)} ${this.dasharray[1] /
        (this.canvasStyleData.scale / 100)}`;
    },
    scaleFountSize() {
      return this.fountSize / (this.canvasStyleData.scale / 100);
    },
    boxWidth() {
      return this.boxModelsData.boxModel1.width;
    },
    boxHeight() {
      return this.boxModelsData.boxModel1.geight;
    }
  },

  methods: {
    genSvg() {
      const { length, width, height, thickness } = this.boxModelsData;
      const r = thickness / 2;
      const M0X = Math.min(width + 2, 45) + r;
      const L1X = M0X + length + 1;
      const L2Y = width / 2 + 1 + r;
      const Q3X2 = L1X + r;
      const Q3Y2 = L2Y + r;
      const Q4X2 = Q3X2 + r;
      const L6X = Q4X2 + width + 1;
      const Q8X2 = L6X + r;
      const Q9X2 = Q8X2 + r;
      const L11X = Q9X2 + length + 1;
      const Q13X2 = L11X + r;
      const Q14X2 = Q13X2 + r;
      const L16X = Q14X2 + width;
      const Q18X2 = L16X + r;
      const L19Y = Q3Y2 + height + 2 + 2;
      const Q20Y2 = L19Y + r;
      const L21Y = Q20Y2 + width / 2 + 1 + r;
      const Q39X2 = M0X - r;
      const L40Y = L19Y - (Q20Y2 - L2Y) / 10 / 2;
      const L41Y = Q3Y2 + (Q20Y2 - L2Y) / 10 / 2;

      const borderPathPoints = [
        { id: "M0", type: "M", shape: { x: M0X, y: 0 } },
        { id: "L1", type: "L", shape: { x: L1X, y: 0 } },
        { id: "L2", type: "L", shape: { x: L1X, y: L2Y } },
        {
          id: "Q3",
          type: "Q",
          shape: {
            x1: L1X,
            y1: L2Y,
            x2: Q3X2,
            y2: Q3Y2,
            cpx1: L1X,
            cpy1: Q3Y2
          }
        },
        {
          id: "Q4",
          type: "Q",
          shape: {
            x1: Q3X2,
            y1: Q3Y2,
            x2: Q4X2,
            y2: L2Y,
            cpx1: Q4X2,
            cpy1: Q3Y2
          }
        },
        { id: "L5", type: "L", shape: { x: Q4X2, y: 0 } },
        { id: "L6", type: "L", shape: { x: L6X, y: 0 } },
        { id: "L7", type: "L", shape: { x: L6X, y: L2Y } },
        {
          id: "Q8",
          type: "Q",
          shape: {
            x1: L6X,
            y1: L2Y,
            x2: Q8X2,
            y2: Q3Y2,
            cpx1: L6X,
            cpy1: Q3Y2
          }
        },
        {
          id: "Q9",
          type: "Q",
          shape: {
            x1: Q8X2,
            y1: Q3Y2,
            x2: Q9X2,
            y2: L2Y,
            cpx1: Q9X2,
            cpy1: Q3Y2
          }
        },
        { id: "L10", type: "L", shape: { x: Q9X2, y: 0 } },
        { id: "L11", type: "L", shape: { x: L11X, y: 0 } },
        { id: "L12", type: "L", shape: { x: L11X, y: L2Y } },
        {
          id: "Q13",
          type: "Q",
          shape: {
            x1: L11X,
            y1: L2Y,
            x2: Q13X2,
            y2: Q3Y2,
            cpx1: L11X,
            cpy1: Q3Y2
          }
        },
        {
          id: "Q14",
          type: "Q",
          shape: {
            x1: Q13X2,
            y1: Q3Y2,
            x2: Q14X2,
            y2: L2Y,
            cpx1: Q14X2,
            cpy1: Q3Y2
          }
        },
        { id: "L15", type: "L", shape: { x: Q14X2, y: 0 } },
        { id: "L16", type: "L", shape: { x: L16X, y: 0 } },
        { id: "L17", type: "L", shape: { x: L16X, y: L2Y } },
        {
          id: "Q18",
          type: "Q",
          shape: {
            x1: L16X,
            y1: L2Y,
            x2: Q18X2,
            y2: Q3Y2,
            cpx1: L16X,
            cpy1: Q3Y2
          }
        },
        { id: "L19", type: "L", shape: { x: Q18X2, y: L19Y } },
        {
          id: "Q20",
          type: "Q",
          shape: {
            x1: Q18X2,
            y1: L19Y,
            x2: L16X,
            y2: Q20Y2,
            cpx1: L16X,
            cpy1: L19Y
          }
        },
        { id: "L21", type: "L", shape: { x: L16X, y: L21Y } },
        { id: "L22", type: "L", shape: { x: Q14X2, y: L21Y } },
        { id: "L23", type: "L", shape: { x: Q14X2, y: Q20Y2 } },
        {
          id: "Q24",
          type: "Q",
          shape: {
            x1: Q14X2,
            y1: Q20Y2,
            x2: Q13X2,
            y2: L19Y,
            cpx1: Q14X2,
            cpy1: L19Y
          }
        },
        {
          id: "Q25",
          type: "Q",
          shape: {
            x1: Q13X2,
            y1: L19Y,
            x2: L11X,
            y2: Q20Y2,
            cpx1: L11X,
            cpy1: L19Y
          }
        },
        { id: "L26", type: "L", shape: { x: L11X, y: L21Y } },
        { id: "L27", type: "L", shape: { x: Q9X2, y: L21Y } },
        { id: "L28", type: "L", shape: { x: Q9X2, y: Q20Y2 } },
        {
          id: "Q29",
          type: "Q",
          shape: {
            x1: Q9X2,
            y1: Q20Y2,
            x2: Q8X2,
            y2: L19Y,
            cpx1: Q9X2,
            cpy1: L19Y
          }
        },
        {
          id: "Q30",
          type: "Q",
          shape: {
            x1: Q8X2,
            y1: L19Y,
            x2: L6X,
            y2: Q20Y2,
            cpx1: L6X,
            cpy1: L19Y
          }
        },
        { id: "L31", type: "L", shape: { x: L6X, y: L21Y } },
        { id: "L32", type: "L", shape: { x: Q4X2, y: L21Y } },
        { id: "L33", type: "L", shape: { x: Q4X2, y: Q20Y2 } },
        {
          id: "Q34",
          type: "Q",
          shape: {
            x1: Q4X2,
            y1: Q20Y2,
            x2: Q3X2,
            y2: L19Y,
            cpx1: Q4X2,
            cpy1: L19Y
          }
        },
        {
          id: "Q35",
          type: "Q",
          shape: {
            x1: Q3X2,
            y1: L19Y,
            x2: L1X,
            y2: Q20Y2,
            cpx1: L1X,
            cpy1: L19Y
          }
        },
        { id: "L36", type: "L", shape: { x: L1X, y: L21Y } },
        { id: "L37", type: "L", shape: { x: M0X, y: L21Y } },
        { id: "L38", type: "L", shape: { x: M0X, y: Q20Y2 } },
        {
          id: "Q39",
          type: "Q",
          shape: {
            x1: M0X,
            y1: Q20Y2,
            x2: Q39X2,
            y2: L19Y,
            cpx1: M0X,
            cpy1: L19Y
          }
        },
        { id: "L40", type: "L", shape: { x: 0, y: L40Y } },
        { id: "L41", type: "L", shape: { x: 0, y: L41Y } },
        { id: "L42", type: "L", shape: { x: Q39X2, y: Q3Y2 } },
        {
          id: "Q43",
          type: "Q",
          shape: {
            x1: Q39X2,
            y1: Q3Y2,
            x2: M0X,
            y2: L2Y,
            cpx1: M0X,
            cpy1: Q3Y2
          }
        },
        { id: "L44", type: "L", shape: { x: M0X, y: 0 } },
        { id: "L45", type: "Z" }
      ];
      const borderpathData = genZrenderPathPoints(borderPathPoints);
      let borderPathStr = genZrenderPathStr(borderPathPoints);
      const border = new zrender.path.createFromString(borderPathStr, {
        style: {
          stroke: "rgb(120,120,120)",
          // opacity: 0.8,
          fill: this.backgroundColor,
          lineWidth: 1
        }
      });

      const wrap = new zrender.Rect({
        style: {
          stroke: "rgb(120,120,120)",
          opacity: 0.8,
          fill: this.backgroundColor,
          lineWidth: 1
        },
        x: -thickness - 2,
        y: -thickness - 2,
        shape: {
          x: M0X - thickness / 2,
          y: 0,
          width: L16X + 3.5 * thickness + 1 - (M0X - thickness / 2),
          height: L21Y + 3 * thickness + 1
        }
      });
      // this.zrEles.push(wrap);
      // this.zrInstance.add(wrap);
      this.zrEles.push(border);
      this.zrInstance.add(border);
      const lineList = [
        { shape: { x1: M0X, y1: L2Y, x2: L1X, y2: L2Y } },
        { shape: { x1: Q4X2, y1: L2Y, x2: L6X, y2: L2Y } },
        { shape: { x1: Q9X2, y1: L2Y, x2: L11X, y2: L2Y } },
        { shape: { x1: Q14X2, y1: L2Y, x2: L16X, y2: L2Y } },

        { shape: { x1: M0X, y1: Q20Y2, x2: L1X, y2: Q20Y2 } },
        { shape: { x1: Q4X2, y1: Q20Y2, x2: L6X, y2: Q20Y2 } },
        { shape: { x1: Q9X2, y1: Q20Y2, x2: L11X, y2: Q20Y2 } },
        { shape: { x1: Q14X2, y1: Q20Y2, x2: L16X, y2: Q20Y2 } },

        // {
        //   shape: {
        //     x1: Q39X2 - thickness - 2,
        //     y1: Q3Y2 - thickness - 2,
        //     x2: Q39X2 - thickness - 2,
        //     y2: L19Y - thickness - 2
        //   },

        //   style: {
        //     lineDash: "none"
        //   }
        // },
        { shape: { x1: Q39X2, y1: Q3Y2, x2: Q39X2, y2: L19Y } },
        { shape: { x1: Q3X2, y1: Q3Y2, x2: Q3X2, y2: L19Y } },
        { shape: { x1: Q8X2, y1: Q3Y2, x2: Q8X2, y2: L19Y } },
        { shape: { x1: Q13X2, y1: Q3Y2, x2: Q13X2, y2: L19Y } }
      ];
      lineList.forEach(line => {
        const wrap = new zrender.Line({
          style: {
            stroke: "rgb(120,120,120)",
            opacity: 0.8,
            lineDash: this.strokeDasharray.map(item => item / (this.canvasStyleData.scale / 100)),
            fill: this.backgroundColor,
            lineWidth: 1,
            ...line.style
          },
          shape: line.shape
        });
        this.zrEles.push(wrap);
        this.zrInstance.add(wrap);
      });

      const mainWrap = document.getElementById("box-modle-wrap");
      const boxViewWidth = L16X + 3.5 * thickness + 1;
      const boxViewHeight = L21Y + 3 * thickness + 1;
      mainWrap.style.width = boxViewWidth + "mm";
      mainWrap.style.height = boxViewHeight + "mm";
      this.$store.commit("setBoxViewStyle", {
        width: boxViewWidth,
        height: boxViewHeight
      });
      const svgWrapDiv = mainWrap.getElementsByTagName("div")[0];
      if (svgWrapDiv) {
        svgWrapDiv.style.overflow = "visible";
        svgWrapDiv.style.width = "100%";
        svgWrapDiv.style.height = "100%";
      }
      const svg = mainWrap.getElementsByTagName("svg")[0];
      if (svg) {
        svg.setAttribute("overflow", "visible");
        svg.setAttribute("width", `${boxViewWidth * 3.75}`);
        svg.setAttribute("height", `${boxViewHeight * 3.75}`);
        svg.setAttribute("viewBox", `-5,-10,${boxViewWidth + 20},${boxViewHeight + 20}`);
      }
    },
    reDraw() {
      this.zrEles.forEach(el => {
        this.zrInstance.remove(el);
      });
      this.zrEles = [];
      this.genSvg();
    }
  }
};
</script>

<style lang="scss" scoped>
.box-modle-2-wrap {
  position: relative;
  box-sizing: border-box;
  svg {
    overflow: visible;
  }
  .background-wrap {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  .background-skeleton-wrap {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
  }
}
</style>
