<template>
  <div @dragstart="handleDragStart" class="component-item drag-able-text">
    <div v-for="(item, index) in List" :key="index" class="drag-able-text-item" draggable :data-index="index">
      <!-- <span class="iconfont" :class="'icon-' + item.icon"></span> -->
      <span :style="labelStyles[index]">{{ item.label }}</span>
    </div>
  </div>
</template>

<script>
import { List } from "@/custom-component/component-list";

export default {
  data() {
    return {
      List,
      labelStyles: [
        { fontSize: "18px", fontWeight: 600 },
        { fontSize: "16px", fontWeight: 500 },
        { fontSize: "14px", fontWeight: 400 }
      ]
    };
  },
  methods: {
    handleDragStart(e) {
      e.dataTransfer.setData("index", e.target.dataset.index);
    }
  }
};
</script>

